import React from 'react';

import { Grid, Card, CardContent, Typography, useTheme, useMediaQuery } from '@mui/material';

import './infoCards.css'

const cardData = [
    {
        title: " ",
        word: "   365",
        description: "",
        example: '    2017'
    },
    {
        title: "",
        word: "   ",
        description: "     ",
        example: ' , , '
    },
    {
        title: "/ ",
        word: "   ",
        description: " ",
        description2: "",
        example: '',
        example2: ''
    },
    {
        title: "      .",
        word: "    ,  .",
        description: "    ",
    },
];

function OutlinedCardsRow() {
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

    return (
        <Grid container spacing={2} sx={{justifyContent: 'space-around', paddingLeft: '16px', paddingRight: '16px'}}>
            {cardData.map((card, index) => (
                <Grid item xs={12} sm={6} md={3} key={index}
                      sx={{
                          maxWidth: isMobile ? '100%' : '300px',
                          width: '100%',
                      }}>
                    <Card variant="outlined" elevation={4} sx={{
                        bgcolor: '#fffaf6',
                        minHeight: 200,
                        boxShadow: '0px 4px 8px rgba(0,0,0,0.2)',
                        width: '100%'
                    }}>
                        <CardContent>
                            <Typography color="text.secondary" gutterBottom>
                                {card.title}
                            </Typography>
                            <Typography variant="h5" component="div">
                                {card.word}
                            </Typography>
                            <Typography variant="body2">
                                {card.description}
                                <br/>
                                {card.example}
                            </Typography>
                            {card.description2 && card.example2 && (
                                <Typography variant="body2">
                                    {card.description2}
                                    <br/>
                                    {card.example2}
                                </Typography>
                            )}
                        </CardContent>
                    </Card>
                </Grid>
            ))}
        </Grid>
    );

}


    export default OutlinedCardsRow;





